	module.exports = {"data":{"talkJson":{"description":"This session, led by James Hamilton, VP and Distinguished Engineer, gives an insider view of some the innovations that help make the AWS cloud unique. He will show examples of AWS networking innovations from the interregional network backbone, through custom routers and networking protocol stack, all the way down to individual servers. He will show examples from AWS server hardware, storage, and power distribution and then, up the stack, in high scale streaming data processing. James will also dive into fundamental database work AWS is delivering to open up scaling and performance limits, reduce costs, and eliminate much of the administrative burden of managing databases. Join this session and walk away with a deeper understanding of the underlying innovations powering the cloud.","meta":{"duration":"PT47M51S"},"presenter":{"name":"James Hamilton"},"slug":"aws-reinvent-2014-or-spot301-aws-innovation-at-scale","source":{"id":"JIQETrFC_SQ","provider":"youtube"},"tag":["AWS re:Invent 2014","Amazon Web Services (AWS"],"title":"AWS Innovation at Scale"},"allTalkJson":null},"pathContext":{"slug":"aws-reinvent-2014-or-spot301-aws-innovation-at-scale","tags":["AWS re:Invent 2014","Amazon Web Services (AWS"]}}

